@import "utils/variables";

*,
*:before,
*:after,
*:first-letter,
*:first-line {
  background: transparent;
  box-shadow: none;
  color: $black;
  text-shadow: none;
}

a,
a:visited {
  text-decoration: underline;
}

a[href]:after {
  content: " (" attr(href) ")";
}

abbr[title]:after {
  content: " (" attr(title) ")";
}

a[href^="#"]:after,
a[href^="javascript:"]:after {
  content: "";
}

pre,
blockquote {
  border: 1px solid $gray;
  page-break-inside: avoid;
}

thead {
  display: table-header-group;
}

tbody {
  display: table-row-group;
}

tr,
img {
  page-break-inside: avoid;
}

td {
  padding: 0;
}

img {
  max-width: 100%;
}

p,
h2,
h3 {
  orphans: 3;
  widows: 3;
}

h2,
h3 {
  page-break-after: avoid;
}

//Library site-specific print css under /sass/sites/library/_print.scss
@import "sites/library/print";
